<template>
    <div id="home-main">
        <swiper :options="swiperOption" @someSwiperEvent="callback" ref="mySwiper"
                style="height: 100%;width: 100%;">
            <swiper-slide>
                <first></first>
            </swiper-slide>
            <swiper-slide>
                <second></second>
            </swiper-slide>
            <swiper-slide>
                <third></third>
            </swiper-slide>
            <swiper-slide>
                <fourth></fourth>
            </swiper-slide>
            <swiper-slide>
                <fifth></fifth>
            </swiper-slide>
            <swiper-slide>
                <sixth></sixth>
            </swiper-slide>
            <swiper-slide>
                <app-footer></app-footer>
            </swiper-slide>
        </swiper>
        <back-top :scroll="pageState" @click="backTop"></back-top>
    </div>
</template>

<script>
  import first from '@/pages/home/home1'
  import second from '@/pages/home/home2'
  import third from '@/pages/home/home3'
  import fourth from '@/pages/home/home4'
  import fifth from '@/pages/home/home5'
  import sixth from '@/pages/home/home6'
  import appFooter from '@/components/app-footer'
  import backTop from '@/components/back-top'

  export default {
    name: 'home',
    components: {
      first,
      second,
      third,
      fourth,
      fifth,
      sixth,
      appFooter,
      backTop
    },
    data() {
      let _this = this
      return {
        swiperOption: {
          // 自定义块大小
          slidesPerView: 'auto',
          // 允许键盘切换
          keyboard: {
            // enabled: true,
            // onlyInViewport: true
          },
          // 禁止鼠标拖动切换
          simulateTouch: false,
          // 开启鼠标滚轮切换
          mousewheel: true,
          // 垂直切换
          direction: 'vertical',
          // 切换事件
          on: {
            // 切换之后的回调方法
            slideChangeTransitionStart: function () {
              _this.$store.commit('setPageState', this.activeIndex)
            }
          }
        }
      }
    },
    mounted() {
      this.$store.commit('setBackground', false)
    },
    methods: {
      backTop() {
        this.$refs.mySwiper.swiper.slideTo(0, 1000, false)
        this.$store.commit('setPageState', 0)
      },
      callback() {
      }
    },
    computed: {
      swiper() {
        return this.$refs.mySwiper.swiper
      },
      pageState() {
        return this.$store.state.pageState
      }
    }
  }
</script>

<style lang="less">
    // 尾页自定义高度
    .swiper-slide:last-child {
        height: 300px;
    }

</style>
